import React, { useState } from 'react'
import AddressForm from './addressForm';

function UnlockableContent() {
  const [unlockCode, setUnlockCode] = useState('');
  const [validUnlockCode, setValidUnlockCode] = useState(null);
  const [previousUnlockCode, setPreviousUnlockCode] = useState(null);

  const unlockableContentChange = evt => {
    setUnlockCode(evt.target.value)
  }

  const updateUnlockCode = val => {
    setUnlockCode(val)
  }

  const checkUnlockableContent = evt => {
    evt.preventDefault();
    switch (unlockCode) {
      case 'vasoVermelho202101': setValidUnlockCode(true); break;
      default: setValidUnlockCode(false);
    }
    setPreviousUnlockCode(unlockCode);
    updateUnlockCode('')
  }

  return (
    <div className="UnlockableContent">
      <div className="UnlockableContent-container">
        <div class="UnlockableContent-help-tip">
          <p>In order to obtain a content code, please visit 
            <a href="https://opensea.io/collection/sparky-labold" target="_blank" className="UnlockableContent-help-tip_link"> opensea </a>
            and place an offer on a minted art piece. Once you have purchased, and received the NFT, it will come with a
            content code, for which you will enter in right here. </p>
        </div>
        <div className="UnlockableContent-description">
          Enter Content Code for Shipping:
        </div>
        <form onSubmit={checkUnlockableContent}>
          <div className="FormInfo">
            <input type="text" id="unlockCode" name="unlockCode" value={unlockCode} placeholder="Code" onChange={unlockableContentChange} className="UnlockableContent-form" />
            <button type="submit" value="submit" className="btn2 btn2--white UnlockableContent-submit">Submit</button>
          </div>
        </form>
        {validUnlockCode === null ? (null) : (validUnlockCode === true) ? (
          <AddressForm unlockCode={previousUnlockCode} />
        ) : <div className="UserError UnlockableContent-error">Please enter in a valid code</div>}
      </div>
    </div>
  );
}

export default UnlockableContent;
